.backgroundContainer {
	z-index: -1;
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
}

.stars {
	background: #000000 url(https://i.postimg.cc/kMKhF9Hf/star.png) repeat;
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	display: block;
}

.twinkling {
	width: 10000px;
	height: 100%;
	background: transparent url('https://i.postimg.cc/mkm9NPWP/twinkling.png') repeat;
	background-size: 1000px 1000px;
	position: absolute;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 1;
	-webkit-animation: move-background 50s linear infinite;
	animation: move-background 50s linear infinite;
}

.idioma {
	text-align: right;
	cursor: pointer;
	width: 60px;
	float: right;
}

.active {
	color: #c3e6e6;
	font-size: 1.1em;
}

.noactive {
	color: #6c757d;
}

.svg {
	position: absolute;
	top: 0;
	left: 0;
	stroke: #d9ffff;
	fill: none;
	stroke-width: 3;
}

.canvas {
	border: 3px solid red;
}

.tardis {
	margin: 0;
	padding: 0;
	position: absolute;
	top: 0;
	left: 0;
	height: 99%;
	width: 100%;
	overflow: hidden;
	z-index: -1;
}

/* ANIMATIONS */
@keyframes move-background {
	from {
		-webkit-transform: translate3d(0px, 0px, 0px);
	}

	to {
		-webkit-transform: translate3d(1000px, 0px, 0px);
	}
}

@-webkit-keyframes move-background {
	from {
		-webkit-transform: translate3d(0px, 0px, 0px);
	}

	to {
		-webkit-transform: translate3d(1000px, 0px, 0px);
	}
}

@-webkit-keyframes move-background {
	from {
		-webkit-transform: translate3d(0px, 0px, 0px);
	}

	to {
		-webkit-transform: translate3d(1000px, 0px, 0px);
	}
}